/**
 * word
 * 
 */
import angularExpressions from 'angular-expressions';
import Docxtemplater from 'docxtemplater'
import PizZip from 'pizzip'
import JSZipUtils from 'jszip-utils'
import { saveAs } from 'file-saver'
/**
 * base64ArrayBuffer
 * @param {Object} dataURL base64
 */
function base64DataURLToArrayBuffer(dataURL) {
    const base64Regex = /^data:image\/(png|jpg|jpeg|svg|svg\+xml);base64,/;
    if (!base64Regex.test(dataURL)) {
        return false;
    }
    const stringBase64 = dataURL.replace(base64Regex, "");
    let binaryString;
    if (typeof window !== "undefined") {
        binaryString = window.atob(stringBase64);
    } else {
        binaryString = Buffer.from(stringBase64, "base64").toString("binary");
    }
    const len = binaryString.length;
    const bytes = new Uint8Array(len);
    for (let i = 0; i < len; i++) {
        const ascii = binaryString.charCodeAt(i);
        bytes[i] = ascii;
    }
    return bytes.buffer;
}

/**
 * word
 * @param {object} tempDocxPath 
 * @param {object} data    
 * @param {object} fileName  
 * @param {object} imgSize  
 */
export const ExportBriefDataDocx = (tempDocxPath, data, fileName,imgSize) => {
    console.log(111, tempDocxPath, data, fileName,imgSize)
    //
    var ImageModule = require('docxtemplater-image-module-free');
    var expressions = require('angular-expressions')
    var assign = require('lodash/assign')
    var last = require("lodash/last")
    expressions.filters.lower = function (input) {
        // This condition should be used to make sure that if your input is
        // undefined, your output will be undefined as well and will not
        // throw an error
        if (!input) return input
        // toLowerCase() 
        return input.toLowerCase()
    }
    function angularParser(tag) {
        tag = tag
            .replace(/^\.$/, 'this')
            .replace(/('|)/g, "'")
            .replace(/(|)/g, '"')
        const expr = expressions.compile(tag)
        return {
            get: function (scope, context) {
                let obj = {}
                const index = last(context.scopePathItem)
                const scopeList = context.scopeList
                const num = context.num
                for (let i = 0, len = num + 1; i < len; i++) {
                    obj = assign(obj, scopeList[i])
                }
                //word $index+1 
                obj = assign(obj, { $index: index })
                return expr(scope, obj)
            }
        }
    }
    //
    JSZipUtils.getBinaryContent(tempDocxPath, (error, content) => {
        if (error) {
            console.log(error)
        }
        expressions.filters.size = function (input, width, height) {
            return {
                data: input,
                size: [width, height],
            };
        };
        let opts = {}
        opts = {
            //
            centered: true
        };
        opts.getImage = (chartId) => {
            //base64ArrayBuffer
            return base64DataURLToArrayBuffer(chartId);
        }
        opts.getSize = function (img, tagValue, tagName) {
            //
            if (imgSize.hasOwnProperty(tagName)) {
                return imgSize[tagName];
            } else {
                return [600, 400];
            }
        }
        // JSZip        
        const zip = new PizZip(content)
        //  Docxtemplater 
        // 
		let doc = new Docxtemplater();
        doc.attachModule(new ImageModule(opts));
        doc.loadZip(zip);
        doc.setOptions({parser:angularParser});
        doc.setData(data) //
        try {
            // 
            doc.render()
        } catch (error) {
            const e = {
                message: error.message,
                name: error.name,
                stack: error.stack,
                properties: error.properties
            }
            console.log('err',{ error: e })
            // json
            throw error
        }
        // Docxtemplaterzip
        const out = doc.getZip().generate({
            type: 'blob',
            mimeType: 'application/vnd.openxmlformats-officedocument.wordprocessingml.document'
        })
        // 
        saveAs(out, fileName)
    })
}
/**
 * urlbase64
 * awaitPromise
 * @param {Object} imgUrl 
 */
export function getBase64Sync(imgUrl) {
    return new Promise(function (resolve, reject) {
        // let
        let image = new Image();
        //
        image.src = imgUrl;
        // 
        image.setAttribute("crossOrigin", '*');  // 
        // image.onload
        image.onload = function () {
            let canvas = document.createElement("canvas");
            canvas.width = image.width; 
            canvas.height = image.height;
            let context = canvas.getContext("2d");
            context.drawImage(image, 0, 0, image.width, image.height);
            //
            let ext = image.src.substring(image.src.lastIndexOf(".") + 1).toLowerCase();
            //
            let quality = 0.8;
            //base64
            let dataurl = canvas.toDataURL("image/" + ext, quality);
            //
            resolve(dataurl);
        };
    })
}




