                            output = mediumFluid;
                            const fluidFlow = output / density;
                            const fluidFlows = fluidFlow
                            this.mediumFluid = fluidFlows.toString()

                        } else if (unit == '11') {
                            output = mediumFluid;
                            const fluidFlow = output / density * 60;
                            const fluidFlows = fluidFlow
                            this.mediumFluid = fluidFlows.toString()

                        } else if (unit == '12') {
                            output = mediumFluid;
                            const fluidFlow = output / density * 3600;
                            const fluidFlows = fluidFlow
                            this.mediumFluid = fluidFlows.toString()

                        } else if (unit == '13') {
                            output = mediumFluid;
                            const fluidFlow = 1000 * output / density;
                            const fluidFlows = fluidFlow
                            this.mediumFluid = fluidFlows.toString()

                        }
                    } else {
                        output = '';
                    }
                    console.log(this.maxFluid); // 
                    bus.$emit('mediumFluidChanged', this.mediumFluid);
                }
            }
            this.$watch('value1', (newValue, oldValue) => {
                if (!isNaN(density) && !isNaN(density1)) {
                let mediumFluid = parseFloat(this.inputTwo);
                console.log("", this.inputTwo)
                // 
                const unit = this.value1[0]; //  selectedUnit 
                let output; // 
                if (!isNaN(unit)) {
                    if (unit == '1' || unit == '2' || unit == '3' || unit == '4' || unit == '5' || unit === '6' || unit == '7' || unit == '8' || unit == '9') {
                        if (unit == '1') {
                            output = mediumFluid;
                            const fluidFlow = output
                            this.mediumFluid = fluidFlow.toFixed(2)
                        } else if (unit == '2') {
                            output = mediumFluid
                            const fluidFlow = output
                            this.mediumFluid = (fluidFlow * 60).toString()
                            console.log("unit == '2'", this.mediumFluid)
                        } else if (unit == '3') {
                            output = mediumFluid
                            const fluidFlow = output
                            this.mediumFluid = (fluidFlow * 3600).toString()
                        } else if (unit == '4') {
                            output = mediumFluid
                            const fluidFlow = output
                            this.mediumFluid = (fluidFlow * 0.001).toString()
                        } else if (unit == '5') {
                            output = mediumFluid
                            const fluidFlow = output
                            this.mediumFluid = (fluidFlow * 0.06).toString()
                        } else if (unit == '6') {
                            output = mediumFluid
                            const fluidFlow = output
                            this.mediumFluid = (fluidFlow * 3.6).toString()
                        } else if (unit == '7') {
                            const conversionFactor = density1 / density;
                            output = mediumFluid * conversionFactor
                            const fluidFlow = output
                            const fluidFlows = fluidFlow
                            this.mediumFluid = fluidFlows.toString()
                        } else if (unit == '8') {
                            const conversionFactor = density1 / density * 60;
                            output = mediumFluid * conversionFactor
                            const fluidFlow = output
                            const fluidFlows = fluidFlow
                            this.mediumFluid = fluidFlows.toString()
                        } else if (unit == '9') {
                            const conversionFactor = density1 / density * 3600;
                            output = mediumFluid * conversionFactor
                            const fluidFlow = output
                            const fluidFlows = fluidFlow
                            this.mediumFluid = fluidFlows.toString()
                        } else {
                            output = '';
                        }
                    } else if (unit == '10' || unit == '11' || unit == '12' || unit == '13') {
                        if (unit == '10') {
                            output = mediumFluid;
                            const fluidFlow = output / density;
                            const fluidFlows = fluidFlow
                            this.mediumFluid = fluidFlows.toString()

                        } else if (unit == '11') {
                            output = mediumFluid;
                            const fluidFlow = output / density * 60;
                            const fluidFlows = fluidFlow
                            this.mediumFluid = fluidFlows.toString()

                        } else if (unit == '12') {
                            output = mediumFluid;
                            const fluidFlow = output / density * 3600;
                            const fluidFlows = fluidFlow
                            this.mediumFluid = fluidFlows.toString()

                        } else if (unit == '13') {
                            output = mediumFluid;
                            const fluidFlow = 1000 * output / density;
                            const fluidFlows = fluidFlow
                            this.mediumFluid = fluidFlows.toString()

                        }
                    } else {
                        output = '';
                    }
                    console.log(this.mediumFluid); // 
                    bus.$emit('mediumFluidChanged', this.mediumFluid);
                }
                }
            });
        },
        compressionCoefficient() {
            if(this.medium=='')
            {
            const temperature = parseFloat(this.inputFive);
            if (isNaN(temperature)) {
                return; // 
            }

            let pressure = parseFloat(this.inputEight);
            if (isNaN(pressure)) {
                return; // 
            }

            let selectedOption = parseInt(this.value2); // 

            const recalculateCompressCoefficient = () => {
                let pressureInKpaAbs = pressure; // 
                switch (selectedOption) {
                    case 2:
                        pressureInKpaAbs += 101.325; // kPa(kPa(
                        break;
                    case 3:
                        pressureInKpaAbs *= 1000; // MPa(kPa(
                        break;
                    case 4:
                        pressureInKpaAbs = pressure * 1000 + 101.325; // MPa(kPa(
                        break;
                    case 5:
                        pressureInKpaAbs *= 101.325; // bar(kPa(
                        break;
                    case 6:
                        pressureInKpaAbs = pressure * 101.325 + 101.325; // bar(kPa(
                        break;
                    default: // kPa(
                        break;
                }
                const compressCoefficient = (pressureInKpaAbs / 101.325) * 273 / (273 + temperature);
                this.compressConvenient = compressCoefficient.toString();
            };

            // 
            recalculateCompressCoefficient();
            // 
            this.$watch('value2', (newValue, oldValue) => {
                if (newValue == oldValue) {
                    return; // 
                }
                console.log(` ${oldValue}  ${newValue}`);
                // 
                selectedOption = parseInt(newValue);
                pressure = parseFloat(this.inputEight);
                recalculateCompressCoefficient();
            });
        }
        },
        calculateViscosity(){
            //
            this.dyviscosity = Number(this.viscosity) / Number(this.inputOnes)
            console.log("", this.dyviscosity)
        },
        // 
        updateViscosity(value, unit) {
            if (value && unit) {
                if (unit == 'kg/ms') {
                    this.viscosity = parseFloat(value);
                } else if (unit == 'mPas') {
                    this.viscosity = 0.001 * parseFloat(value);
                } else if (unit == 'cP') {
                    this.viscosity = 0.001 * parseFloat(value);
                } else {
                    console.log("");
                    return;
                }
                console.log("kg/ms", this.viscosity);
                this.calculateViscosity()
                console.log("",this.dyviscosity)
            }
        }
    },
    mounted() {
        this.$watch('value4', (newValue, oldValue) => {
            console.log('', newValue);
            if (newValue && newValue.length > 0) {
                const unit = this.optionsFour.find(option => option.value === newValue[0]).label;
                this.updateViscosity(this.inputFives, unit);
            }
        });
        this.$watch('inputOnes', (newValue, oldValue) => {
            this.calculateViscosity(); // 
            bus.$emit('inputOnes', newValue);
        });
        // 
        this.$watch('inputFives', (newValue, oldValue) => {
            if (newValue || newValue === 0) {
                this.updateViscosity(newValue, this.value4[0] ? this.optionsFour.find(option => option.value === this.value4[0]).label : '');
            }
        });
        this.value5 = [this.optionsThress[0].value];
        this.value3 = [this.optionsOnes[0].value];
        this.value7 = [this.optionsFive[0].value];
        this.handleDefaultSelection2()
        this.handleDefaultSelection1()
        // 
        this.handleDefaultSelection()
        // this.calculatemaxFlowSpeed()
        bus.$on('density-change', (density) => {
            console.log(':', density);
            console.log(typeof (density))//number
            console.log(typeof (this.compressConvenient))//String
            console.log("density:", density);
            console.log("compressConvenient:", this.compressConvenient);
            if(this.medium==''){
            this.inputOnes = Number(density) * Number(this.compressConvenient);
            this.$watch('compressConvenient', (newVal, oldVal) => {
                this.inputOnes = Number(density) * Number(newVal);
            });
        }else if(this.medium==''){
            this.inputOnes=density
        }
            this.inputTens = density
            console.log(typeof (this.inputOnes))
            console.log(this.inputOnes)
            // this.pressureLossion()
        });
        bus.$on('viscosity-change', (viscosity) => {
            console.log(':', viscosity);
            if (viscosity == 0 && (this.medium == '' || this.medium == '')) {
                //
            } else {
                this.inputFives = viscosity * 0.001; // kg/mscP
                this.viscosity = viscosity;
                console.log("this.inputFives", this.inputFives);
            }
            console.log("", this.viscosity); // this.inputFives
            this.calculateViscosity();     
        });
        bus.$on('medium-change', (medium) => {
            this.medium=medium
            console.log("", medium);
            if (medium === '') {
                this.inputTens1 = 'kg/m3';
                // console.log("1234", this.inputTens1)
            } else if (medium === '') {
                this.compressConvenient=''
                this.inputTens1 = 'kg/m3';
            } else if(medium === ''){
                this.compressConvenient=''
            }
        });
    },
    watch: {
        dyviscosity(newVal) {
            bus.$emit('dyviscosityChanged', newVal);
            console.log('', newVal);
        },
        value4(newValue, oldValue){
            this.handleSelectChange2();
        },
        value1(newValue, oldValue) {
            //  value1  handleSelectChange() 
            this.handleSelectChange();
        },
        value2(newValue, oldValue) {
            //  value1  handleSelectChange() 
            this.handleSelectChange1();
        },
        processData: {
            handler(newVal, oldVal) {
                this.processData1 = newVal
                console.log(":", this.processData1)
            }
        },
        outcmomeData: {
            handler(newVal, oldVal) {
                this.outcmomeData1 = newVal
                console.log(":", this.outcmomeData1)
            }
        },
        fluidData: {
            handler(newVal, oldVal) {
                console.log("", newVal)
                const firstItem = newVal[''];
                const secondItem = newVal[''];
                const thirdItem = newVal[''];
                const fourItem = newVal['']
                const fiveItem = newVal['']
                const sixItem = newVal['']
