                this.inputTwos = fourItem['']
                this.inputFours = fiveItem['']
                this.inputFives = fiveItem['']
                this.inputSixs = fiveItem['']
                 this.inputSevens = sixItem['']
                this.inputEights = sixItem['']
                this.inputNines = sixItem['']
            }
        },
    },
    computed: {
    },


};  
