<template>
    <div id="Process">
        <div class="biaotilan">
            <div class="container"> Proccess parameter</div>
            <div class="sanjiao"></div>
        </div>
        <div class="request">
            <span class="span2"> </span> &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            <div class="option-group">
          <label>
            <input type="radio" v-model="selectedOption" value="" @change="resetSelectedValues('')">
            
          </label>
          <common-select v-model="selected1" :options="optionsOne1" :is-disabled="selectedOption!== '' " class="select" @input="handleChange1"></common-select>
            </div>
          <div class="option-group">
          <label>
            <input type="radio" v-model="selectedOption" value="" @change="resetSelectedValues('')">
            
          </label>
          <common-select v-model="selected2" :options="optionsOne2" :is-disabled="selectedOption!== '' " class="select" @input="handleChange2"></common-select>
          </div>
          <div class="option-group">
          <label>
            <input type="radio" v-model="selectedOption" value="" @change="resetSelectedValues('')">
            
          </label>
          <common-select v-model="selected3" :options="optionsOne3" :is-disabled="selectedOption!== '' " class="select" @input="handleChange3"></common-select>
        </div>
            <!-- <common-select v-model="selected1" :options="optionsOne" class="select" @input="handleChange"></common-select> -->
            <!-- :value="selected"selected -->
        </div>
        <div class="request">
            <span> </span> &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            <common-select :value="value" :options="optionsTwo" class="select"></common-select>
        </div>
        <div class="request">
            <span> </span> &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            <el-input v-model="compressConvenient" placeholder="" class="inputOne"
                ></el-input>
            <!-- v-model :compressConvenient="CompressConvenient" -->
        </div>
        <div class="request1">
            <span class="span1">/(mm)</span> &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            <el-input v-model="inputTwo" placeholder="/" class="input" @input="handleInputChange"></el-input>
        </div>
    </div>
</template>

<script>
import CommonSelect from '@/components/el-select/CommonSelect.vue'
import {bus} from '@/main.js'
import { getMedius,getSteammedius } from '@/api/select/flowmeter_medius' 
//  Process 
export default {
    props: {
        compressConvenient: {
            type: String
        },
        processData1:{
            type:Array,
      default:()=>([])
        }
    },//prop
    name: 'Process',
    components: {
        CommonSelect
    },
    data() {
        return {
            mediutables_id:'',//id
            process_viscosity:[],//
            processrealviscosity:'',//
            process_density:[],//
            processrealdensity:'',//
            // value1:'',
            selectreal:'',//--
            selectedOption:[],
            inputOne: '',
            inputTwo: '',//
            value: [],
            selected1: [],
            selected2: [],
            selected3: [],
            optionsOne1: [
                {
                    value:'1',
                    label:''
                }
            ],
            optionsOne2: [
            ],
            optionsOne3: [
            ],
            optionsTwo: [
                {
                    value: '1',
                    label: ''
                },
                {
                    value: '2',
                    label: ''
                }
            ],
            sselectreal:''//Specsword
        }
    },
    mounted(){   
    },
    created(){
        bus.$on('inputTwentyChanged', (newVal) => {
    console.log('Process:', newVal);
    this.sselectreal=newVal
    console.log('Process:', this.sselectreal);
    // 
    // newVal
  });
    },
    methods: {
        getMediusData() {
            getMedius({ type_name: this.selectreal }).then((res) => {
                // 
                let dataList = Array.from(res.rows);
                console.log("", dataList)
                for (let item of dataList) {
                    if (this.selectreal == '') {
                        // 
                        this.optionsOne1 = dataList.map(function (item) {
                            return { value: item.id, label: item.media_name, };
                        });
                        this.process_density=dataList.map(function (item) {
                            return {value: item.id,label: item.standar_density};
                        });
                        this.process_viscosity=dataList.map(function (item) {
                            return {value: item.id,label: item.viscosity};
                        });
                    } else if (this.selectreal == '') {
                        // 
                        this.optionsOne2 = dataList.map(function (item) {
                            return { value: item.id, label: item.media_name, };
                        });
                        this.process_density=dataList.map(function (item) {
                            return {value: item.id,label: item.standar_density};
                        });
                        this.process_viscosity=dataList.map(function (item) {
                            return {value: item.id,label: item.viscosity};
                        });
                    }
                }
            }).catch((err) => {
                console.error(err); // 
            });
        },
        getSteammediusData() {
            getSteammedius({ type_name:this.selectreal}).then((res) => {
                // 
                console.log("",this.selectreal)
                 bus.$emit('medium-change', this.selectreal);//
                let dataList = Array.from(res.rows);
                this.optionsOne3 = dataList.map(function(item) {
               return { value: item.id, label: item.media_name, };

            });
            }).catch((err) => {
                    console.error(err); // 
                });
        },
        resetSelectedValues(option) {
            if (option !== '') {
                this.selected1 = [];
            }
            if (option !== '') {
                this.selected2 = [];
            }
            if (option !== '') {
                this.selected3 = [];
            }
        },
        sendDiameterToFluid() {
            bus.$emit('update-diameter', this.inputTwo);
        },
        handleInputChange() {
            console.log(this.inputTwo)
            // 
            this.sendDiameterToFluid();
        },
        handleChange1() {
            const medium = this.selected1;//
            // const medium =selectedOption.label
            console.log('', medium);
            const selectedOption = this.optionsOne1.find(option => option.value === this.selected1[0]);
            console.log(this.selected1[0])
            bus.$emit('selected1-changed', this.selected1[0]);
            console.log('', selectedOption.label);
            const targetDensity = this.process_density.find(item => item.value === this.selected1[0]);
            if (targetDensity) { // 
                this.processrealdensity = targetDensity.label;
                console.log(':', this.processrealdensity);
            } else { // 
                console.log('');
                this.processrealdensity = ''; //  processrealdensity 
            }
            bus.$emit('density-change', this.processrealdensity);//
            const targetDensity1 = this.process_viscosity.find(item => item.value === this.selected1[0]);
            if (targetDensity1) { // 
                this.processrealviscosity = targetDensity1.label;
                console.log(':', this.processrealviscosity);
            } else { // 
                console.log('');
                this.processrealdensity = ''; //  processrealdensity 
            }
            bus.$emit('viscosity-change', this.processrealviscosity);//
        },
        handleChange2() {
            const medium = this.selected2;//
            console.log('', medium);
            bus.$emit('selected2-changed', this.selected2[0]);
            const selectedOption = this.optionsOne2.find(option => option.value === this.selected2[0]);
            console.log('', selectedOption.label);
            const targetDensity = this.process_density.find(item => item.value === this.selected2[0]);
            if (targetDensity) { // 
                this.processrealdensity = targetDensity.label;
                console.log(':', this.processrealdensity);
            } else { // 
                console.log('');
                this.processrealdensity = ''; //  processrealdensity 
            }
            bus.$emit('density-change', this.processrealdensity);
            const targetDensity1 = this.process_viscosity.find(item => item.value === this.selected2[0]);
            if (targetDensity1) { // 
                this.processrealviscosity = targetDensity1.label;
                console.log(':', this.processrealviscosity);
            } else { // 
                console.log('');
                this.processrealdensity = ''; //  processrealdensity 
            }
            bus.$emit('viscosity-change', this.processrealviscosity);
        },
        handleChange3() {
            const medium = this.selected3;//
            // const medium =selectedOption.label
            console.log('', medium);
            bus.$emit('selected3-changed', this.selected3[0]);
            const selectedOption = this.optionsOne3.find(option => option.value === this.selected3[0]);
            console.log('', selectedOption.label);
        }
    },
    watch: {
    selectedOption(newValue, oldValue) {
        this.selectreal=newValue
        bus.$emit('selectnameChanged', this.selectreal);
      if (newValue !== '') {
        this.resetSelectedValues(newValue);
        this.getMediusData()// getMediusData()
        this.getSteammediusData()
      }
    },
    processData1: {
            handler(newVal, oldVal) {
                console.log(":", newVal)
                console.log("specs:",this.sselectreal)
                if (Object.keys(newVal).length > 0) {
                    this.getMediusData()
                    const data = newVal; // projectData
                    this.compressConvenient = data.
                    this.inputTwo = data['/(mm)']
                    // 
                    const Type = data.[0];
                    console.log(Type)
                    // value
                    const match = this.optionsTwo.find(option => option.label == Type);
                    console.log("",match)
                    // valueselectedValue
                    if (match) {
                        this.value = [match.value];
                    }
                    //this.optionsOne1, this.optionsOne2, this.optionsOne3
                    const combinedArray = [this.optionsOne1, this.optionsOne2, this.optionsOne3];
                    //   console.log(combinedArray)
                    const Type1 = data.[0]
                    console.log(Type1)
                    const mach1 = combinedArray[0].find(option => option.label == Type1)
                    console.log("process",mach1)
                    const mach2 = combinedArray[1].find(option => option.label == Type1)
                    const mach3 = combinedArray[2].find(option => option.label == Type1)
                    if (mach1) {
                        this.selected1 = [mach1.value]
                    } else if (mach2) {
                        this.selected2 = [mach2.value]
                    } else if (mach3) {
                        this.selected3 = [mach3.value]
                    }
                }
            }
        },
  compressConvenient:{
    handler(newVal, oldVal){
        console.log(":",newVal)
    }
  }
}
}
</script>

