<template>
  <div id="selectType">
    <div class="all">
      <div class="biaotilan">
        <div class="container"> Project</div>
        <div class="sanjiao"></div>
      </div>
      <div class="request">
        <span> </span> &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
        <el-input v-model="inputOne" placeholder="" class="input"></el-input>
      </div>
      <div class="request">
        <span> </span> &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
        <el-input v-model="inputTwo" placeholder="" class="input"></el-input>
      </div>
      <div class="request1">
        <span></span> &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
        <common-select v-model="selectedValue" :options="optionsOne" class="select"></common-select>
      </div>
      <div class="request">
        <span> </span> &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
        <el-input v-model="inputThree" placeholder="" class="input"></el-input>
      </div>
      <div class="request">
        <span>/</span> &nbsp;
        <el-input v-model="inputFour" placeholder="" class="input"></el-input>
      </div>
    </div>
  </div>
</template>

<script>
import CommonSelect from '@/components/el-select/CommonSelect.vue'
import { getFlowmeter } from '@/api/select/flowmeter_medius';
import {bus} from '@/main.js'



export default {
  name: "selectType",
  components:{
    CommonSelect
  },
  props:{
    projectData:{
      type:Array,
      default:()=>([])
    }
  },
  data() {
    return {
      inputOne: '',
      inputTwo: '',
      inputThree: '',
      inputFour: '',
      selectedValue: [],
      optionsOne:[],
      flowmeter_type:'',//
      flowmeter_type_encode:'',//
      flow_encoed:'',//
    };
  },
  mounted() {
    this.getData();
},
  watch: {
    selectedValue: function(newVal, oldVal) {
    if (newVal.length > 0) {
      // console.log('', newVal[0]);
      this.flowmeter_type=newVal[0]
      console.log('', this.flowmeter_type);
    }
  },
    projectData: {
  handler(newVal, oldVal) {
    console.log('projectData', newVal);
    if (Object.keys(newVal).length > 0) {
      const data = newVal; // projectData
      this.inputOne = data. || '';
      this.inputTwo = data. || '';
      this.inputThree = data. || '';
      this.inputFour = data['/'] || ''; // 
      // this.selectedValue = data. || [];
      // 
      const flowMeterType = data.[0];
      console.log(flowMeterType)
      // value
      const match = this.optionsOne.find(option => option.label == flowMeterType);
      console.log("id",match)
      // valueselectedValue
      if (match) {
        this.selectedValue = [match.value];
      }
    }
    }
  },
  immediate: true
},
methods:{
  // 
  getData() {
      this.optionsOne = []
      getFlowmeter().then((res) => {
  // console.log(res);
  let dataList = Array.from(res.rows); // Array.from()
  // console.log(dataList);
  this.optionsOne = dataList.map(function(item) {
    return { value: item.id, label: item.type_name, };
  });
  this.flowmeter_type_encode = dataList.map(function(items) {
    return {id: items.id, encode:items.encode };
  });
  // console.log(this.flowmeter_type_encode)
})
  },
},
created() {
    //  flowmeter_type 
    this.$watch('flowmeter_type', function(newVal, oldVal) {
      console.log(newVal)
      bus.$emit('flowmeterTypeChanged', newVal);
      console.log(this.flowmeter_type_encode)
      var selectedObject = this.flowmeter_type_encode.find(item => item.id === parseInt(newVal));
  if (selectedObject) {
    this.flow_encoed = selectedObject.encode;
    console.log("encode ", this.flow_encoed);
  } else {
    console.error(" id  encode ");
  }
  bus.$emit('flowmeterEncodeTypeChanged', this.flow_encoed);
});
  }
}
</script>

