<script>
import * as echarts from 'echarts';
export default {
    name: 'Loss',
    props:{
    measurementData:{
      type:Array,
      default:()=>([])
    },
    lossImage: {
            type: Array,
            default: () => ([])
        }
  },
    data() {
  return {
    inputs: [
      [null, null, null, null, null, null, null, null, null, null, null],
      [null, null, null, null, null, null, null, null, null, null, null],
      [null, null, null, null, null, null, null, null, null, null, null],
      [null, null, null, null, null, null, null, null, null, null, null],
    ],
    chartImageUrl:[""]//Base64
  }
},
methods: {
  validateInputs() {
      this.drawChart();
    },
    async drawChart() {
      // this.chartLoading = true;
      const chart = echarts.init(this.$refs.chart);
      // 
      const flowPercentage = this.inputs[0];
      const flowKgPerHour = this.inputs[1];
      const measurementError = this.inputs[2];
      const pressureLoss = this.inputs[3];
      // 
      const option = {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
          },
          formatter: "{b}: {c}"
        },
        xAxis: {
          type: 'category',
          data: flowKgPerHour,
          name: '(kg/h)',
          axisLabel: {
            fontSize: 9 //  10px
          }
        },
        yAxis: [
          {
            type: 'value',
            name: '(%)',
            min: 0,
            max: 2,
            interval: 0.2
          },
          {
            type: 'value',
            name: '(KPa)',
            min: 0,
            max: 120,
            interval: 20
          }
        ],
        legend: {
          data: ['', ''],
          bottom: 10,
          formatter: function(name) {
            var color = '';
            if (name == '') {
              color = 'blue';
            } else if (name == '') {
              color = 'orange';
            }
            return name;
          }
        },
        series: [
          {
            name: '',
            type: 'line',
            data: measurementError,
            itemStyle: {
              normal: {
                color: 'blue'
              }
            }
          },
          {
            name: '',
            type: 'line',
            yAxisIndex: 1,
            data: pressureLoss,
            itemStyle: {
              normal: {
                color: 'orange'
              }
            }
          }
        ]
      };
      
      // 
      chart.setOption(option);
    }
  },
  watch: {
  inputs: {
    handler() {
      this.validateInputs();
    },
    deep: true
  },
  measurementData: {
  handler(newVal, oldVal) {
    //Vue
    //Vue$set
    for (const key in newVal) {
      if (newVal.hasOwnProperty(key)) {
        const arr = newVal[key];
        for (let i = 0; i < arr.length; i++) {
          const value = arr[i];
          if (key === '%') {
            this.$set(this.inputs[0], i, value);
          } else if (key === 'kg/h') {
            this.$set(this.inputs[1], i, value);
          } else if (key === '%') {
            this.$set(this.inputs[2], i, value);
          } else if (key === '(KPa)') {
            this.$set(this.inputs[3], i, value);
          }
        }
      }
    }
    console.log('inputs:', this.inputs);
    this.validateInputs();
  },
  deep: true
},
  lossImage: {
            handler(newVal, oldVal) {
                console.log("loss", newVal)
                const imageData = newVal
                this.chartImageUrl[0] = imageData[0].src
                // console.log("src",this.chartImageUrl[0])
            }
        }
}
}

</script>

