<template>
    <div id="Chat">
        <div class="container"> Chart</div>
        <div class="request">
            <span></span>
            <span class="textOne"></span>
        </div>
        <div class="request">
            <span></span> &nbsp;&nbsp;&nbsp;
            <el-input  v-model="ChartData.inputOne" placeholder="" class="input"></el-input>
            <el-input v-model="ChartData.inputTwo" placeholder="" class="input"></el-input>
            <common-select :value="ChartData.value" :options="ChartData.optionsOne" class="select"></common-select>
        </div>
        <div class="request">
            <span> </span> &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            <el-input v-model="ChartData.inputThree" placeholder="" class="input"></el-input>
            <el-input v-model="ChartData.inputFour" placeholder="" class="input"></el-input>
            <common-select :value="ChartData.value" :options="ChartData.optionsOne" class="select"></common-select>
        </div>
    </div>
</template>
<script>
import CommonSelect from '@/components/el-select/CommonSelect.vue'
import {bus} from '@/main.js'
export default {
    name: 'Chat',
    props:{
      chartData: {
            type: Array,
            default: () => ([])
        },

    },
   
    components:{
        CommonSelect
    },
    data(){
        return{
            ChartData:{
            inputOne: '',
            inputTwo: '',
            inputThree: '',
            inputFour: '',
            optionsOne:[
            {
                    value: '1',
                    label: 'm3/h',
                },
                {
                    value: '2',
                    label: 'm3/min'
                },
                {
                    value: '3',
                    label: 'm3/s'
                },
                {
                    value: '4',
                    label: 'L/h'
                },
                {
                    value: '5',
                    label: 'l/min'
                },
                {
                    value: '6',
                    label: 'l/s'
                },
                {
                    value: '7',
                    label: 'Nm3/h'
                },
                {
                    value: '8',
                    label: 'Nm3/min'
                },
                {
                    value: '9',
                    label: 'Nm3/s'
                },
                {
                    value: '10',
                    label: 'kg/h'
                },
                {
                    value: '11',
                    label: 'kg/min'
                },
                {
                    value: '12',
                    label: 'kg/s'
                },
                {
                    value: '13',
                    label: 't/h'
                }
            ],
            value:[]
            }
        }
    },
    methods: {
  updateChartData() {
    // chartData
    this.ChartData = {
        inputOne: this.ChartData.inputOne,
      inputTwo: this.ChartData.inputTwo,
      inputThree: this.ChartData.inputThree,
      inputFour: this.ChartData.inputFour,
      value: this.ChartData.value,
      optionsOne: this.ChartData.optionsOne
      
    };
    console.log("chart",this.ChartData)
    // chartData
    bus.$emit('chart-data-updated', this.ChartData);
    
  },
    handleSelectValueChanged(value) {
    console.log(':', value);
    this.ChartData.value=value
    console.log("",this.ChartData.value)
    const selectedOption = this.ChartData.optionsOne.find(option => option.value == value);
      //  value  selectedOptionValue null
      if (selectedOption) {
        this.ChartData.value= [selectedOption.value];
      } else {
        this.ChartData.value= [];
      }

  },
},
watch: {
  chartData:{
        handler(newVal, oldVal) {
                console.log("chart:", newVal)
                console.log("dayinl",newVal)
                const firstItem = newVal[''];
                const secondItem = newVal[''];
                this.ChartData.inputOne = firstItem[''];
                this.ChartData.inputTwo = firstItem[''];
                this.ChartData.inputThree = secondItem[''];
                this.ChartData.inputFour = secondItem['']
            }
        },
    // 
    'ChartData.inputOne'() {
      this.updateChartData()
    },
    'ChartData.inputTwo'() {
      this.updateChartData()
    },
    'ChartData.inputThree'() {
      this.updateChartData()
    },
    'ChartData.inputFour'() {
      this.updateChartData()
    },
    'ChartData.value': {
      handler: function (newVal, oldVal) {
        if (JSON.stringify(newVal) !== JSON.stringify(oldVal)) {
          this.updateChartData()
        }
      },
      deep: true
    },
    'ChartData.optionsOne': {
      handler: function (newVal, oldVal) {
        if (JSON.stringify(newVal) !== JSON.stringify(oldVal)) {
          this.updateChartData()
        }
      },
      deep: true
    }
  },
  mounted(){
     bus.$on('select-value-changed', this.handleSelectValueChanged);
  }
}
</script>

<style scoped>
 
#Chat{
   position: absolute;
    margin-top: 40px;
    margin-left:110px;
    background-color: #ffff ;
    width: 1207.5px;
    border-left: 1px solid rgb(217, 210, 210);
  border-right: 1px solid rgb(217, 210, 210);
  border-bottom: 1px solid rgb(217, 210, 210);
  border: 1px solid rgb(217, 210, 210);
  height: 200px;
  border-radius: 10px; 
}
.container{
    width: 1207.5px;
    height: 45px;
    background-color: rgba(227, 227, 227, 0.667); 
    line-height: 45px;
    /* left:128px; */
}
.textOne{
    margin-right: 120px;
    margin-left: 380px;
}
.request{
    margin-top: 10px;
    font-size: 13px;
}
.input{
    width: 383px;
    margin-left: 10px;
}
.select{
    margin-left: 10px;
}
</style>