                                    inputEightValues.push(this.inputEight); // inputEight
                                    console.log(`rebore_id: ${value}, inputFive: ${this.inputFive}, inputEight: ${this.inputEight}`);
                                    if (this.minFluid >= this.inputFive && this.minFluid <= this.inputEight && this.maxFluid >= this.inputFive && this.maxFluid <= this.inputEight) {
                                        matchedCalibers.push({
                                            value:value,
                                            label: label,
                                            inputFive: this.inputFive,
                                            inputEight: this.inputEight
                                        }); // 
                                    }else{
                                        //,
                                        if(this.mediumFluid>=this.inputFive&&this.mediumFluid<=this.inputEight&&this.maxFluid >= this.inputFive && this.maxFluid <= this.inputEight){
                                            matchedCalibers.push({
                                            value:value,
                                            label: label,
                                            inputFive: this.inputFive,
                                            inputEight: this.inputEight
                                        });
                                        }else{

                                        }

                                    }
                                })
                            );
                        }
                        //  Promise.all() 
                        Promise.all(promises)
                            .then(() => {
                                console.log("", matchedCalibers); // 
                                matchedCalibers.sort((a, b) => a.label - b.label); // 
                                const minCaliber = matchedCalibers[0]; // 
                                this.name = [minCaliber.value];  // this.inputTwo
                                this.inputFive = minCaliber.inputFive;    // inputFivethis.inputFive
                                this.inputEight = minCaliber.inputEight;  // inputEightthis.inputEight
                                console.log("", minCaliber);
                                resolve({
                                    inputFive: minCaliber.inputFive,
                                    inputEight: minCaliber.inputEight,
                                    selectedCaliber: minCaliber.label
                                });
                            })
                            .catch((error) => {
                                reject(error);
                            });
                    })
                    .catch((error) => {
                        reject(error);
                    });
                    
            });
            
        },
        
    },
mounted(){
        bus.$on('density-change', (density) => {
            console.log('Size:', density);
            this.density=density
            // ...
        });
        
        

}
}
    
